import React, { useState } from 'react';
import { X, LogIn, UserPlus, AlertCircle } from 'lucide-react';
import { useAuth } from '../context/AuthContext';

const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';

export default function AuthForm({ onClose }) {
  const { login } = useAuth();
  const [isRegister, setIsRegister] = useState(false);
  const [formData, setFormData] = useState({
    username: '',
    password: '',
    confirmPassword: ''
  });
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
    setError(''); // Clear error when user types
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    setError('');

    // // Validation
    // if (isRegister) {
    //   if (formData.password !== formData.confirmPassword) {
    //     setError('Mt khu xc nhn khng khp!');
    //     setIsLoading(false);
    //     return;
    //   }
    //   if (formData.password.length < 6) {
    //     setError('Mt khu phi c t nht 6 k t!');
    //     setIsLoading(false);
    //     return;
    //   }
    //   if (formData.username.length < 3) {
    //     setError('Tn ng nhp phi c t nht 3 k t!');
    //     setIsLoading(false);
    //     return;
    //   }
    // }

    try {
      if (isRegister) {
        // Register
        const response = await fetch(`${API_BASE_URL}/register`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            username: formData.username,
            password: formData.password
          })
        });

        if (!response.ok) {
          const error = await response.json();
          throw new Error(error.error || error.message || 'ng k tht bi');
        }

        const data = await response.json();
        alert(' ng k thnh cng! Vui lng ng nhp.');
        
        // Switch to login mode and pre-fill username
        setIsRegister(false);
        setFormData({
          username: formData.username,
          password: '',
          confirmPassword: ''
        });
        
      } else {
        // Login
        const response = await fetch(`${API_BASE_URL}/login`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            username: formData.username,
            password: formData.password
          })
        });

        if (!response.ok) {
          const error = await response.json();
          throw new Error(error.error || error.message || 'ng nhp tht bi');
        }

        const data = await response.json();
        
        // Save to AuthContext
        login({
          userId: data.userId,
          username: data.username
        }, data.token);

        alert(' ng nhp thnh cng!');
        onClose();
      }
    } catch (err) {
      console.error('Auth error:', err);
      setError(err.message || 'C li xy ra. Vui lng th li!');
    } finally {
      setIsLoading(false);
    }
  };

  const toggleMode = () => {
    setIsRegister(!isRegister);
    setError('');
    setFormData({
      username: '',
      password: '',
      confirmPassword: ''
    });
  };

  return (
    <div className="relative">
      {/* Close Button */}
      <button
        onClick={onClose}
        className="absolute top-0 right-0 p-2 text-gray-400 hover:text-gray-600 dark:hover:text-gray-300 transition"
      >
        <X size={24} />
      </button>

      {/* Header */}
      <div className="mb-6">
        <div className="flex items-center justify-center mb-4">
          <div className="bg-gradient-to-br from-blue-500 to-indigo-600 p-3 rounded-full">
            {isRegister ? (
              <UserPlus className="text-white" size={28} />
            ) : (
              <LogIn className="text-white" size={28} />
            )}
          </div>
        </div>
        <h2 className="text-2xl font-bold text-center text-gray-900 dark:text-white">
          {isRegister ? 'ng k ti khon' : 'ng nhp'}
        </h2>
        <p className="text-center text-gray-600 dark:text-gray-400 mt-2 text-sm">
          {isRegister 
            ? 'To ti khon mi  lu tr ha n' 
            : 'ng nhp  truy cp lch s ha n'}
        </p>
      </div>

      {/* Form */}
      <form onSubmit={handleSubmit} className="space-y-4">
        {/* Username */}
        <div>
          <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Tn ng nhp
          </label>
          <input
            type="text"
            name="username"
            value={formData.username}
            onChange={handleChange}
            className="w-full px-4 py-3 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-gray-700 dark:text-white transition"
            placeholder="Nhp tn ng nhp"
            required
            minLength={3}
          />
        </div>

        {/* Password */}
        <div>
          <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Mt khu
          </label>
          <input
            type="password"
            name="password"
            value={formData.password}
            onChange={handleChange}
            className="w-full px-4 py-3 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-gray-700 dark:text-white transition"
            placeholder="Nhp mt khu"
            required
            minLength={6}
          />
        </div>

        {/* Confirm Password (Register only) */}
        {isRegister && (
          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
              Xc nhn mt khu
            </label>
            <input
              type="password"
              name="confirmPassword"
              value={formData.confirmPassword}
              onChange={handleChange}
              className="w-full px-4 py-3 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-gray-700 dark:text-white transition"
              placeholder="Nhp li mt khu"
              required
              minLength={6}
            />
          </div>
        )}

        {/* Error Message */}
        {error && (
          <div className="flex items-center gap-2 p-3 bg-red-50 dark:bg-red-900/30 border border-red-200 dark:border-red-800 rounded-lg">
            <AlertCircle className="text-red-600 dark:text-red-400" size={20} />
            <span className="text-sm text-red-600 dark:text-red-400">{error}</span>
          </div>
        )}

        {/* Submit Button */}
        <button
          type="submit"
          disabled={isLoading}
          className="w-full bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 disabled:from-gray-400 disabled:to-gray-500 text-white font-semibold py-3 px-4 rounded-lg transition duration-200 flex items-center justify-center gap-2"
        >
          {isLoading ? (
            <>
              <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
              <span>ang x l...</span>
            </>
          ) : (
            <>
              {isRegister ? <UserPlus size={20} /> : <LogIn size={20} />}
              <span>{isRegister ? 'ng k' : 'ng nhp'}</span>
            </>
          )}
        </button>

        {/* OAuth2 Divider */}
        {!isRegister && (
          <div className="relative my-6">
            <div className="absolute inset-0 flex items-center">
              <div className="w-full border-t border-gray-300 dark:border-gray-600"></div>
            </div>
            <div className="relative flex justify-center text-sm">
              <span className="px-2 bg-white dark:bg-gray-800 text-gray-500 dark:text-gray-400">hoc</span>
            </div>
          </div>
        )}

        {/* Google OAuth Button */}
        {!isRegister && (
          <button
            type="button"
            onClick={() => window.location.href = `${API_BASE_URL}/google`}
            className="w-full bg-white dark:bg-gray-700 border border-gray-300 dark:border-gray-600 hover:bg-gray-50 dark:hover:bg-gray-600 text-gray-700 dark:text-gray-300 font-semibold py-3 px-4 rounded-lg transition duration-200 flex items-center justify-center gap-2"
          >
            <svg className="w-5 h-5" viewBox="0 0 24 24">
              <path fill="#4285F4" d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"/>
              <path fill="#34A853" d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"/>
              <path fill="#FBBC05" d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"/>
              <path fill="#EA4335" d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"/>
            </svg>
            <span>ng nhp vi Google</span>
          </button>
        )}
      </form>

      {/* Toggle Mode */}
      <div className="mt-6 text-center">
        <button
          onClick={toggleMode}
          className="text-blue-600 dark:text-blue-400 hover:underline text-sm font-medium"
        >
          {isRegister 
            ? '  c ti khon? ng nhp ngay' 
            : 'Cha c ti khon? ng k '}
        </button>
      </div>

      {/* Guest Mode Reminder */}
      <div className="mt-6 pt-6 border-t border-gray-200 dark:border-gray-700">
        <p className="text-xs text-gray-500 dark:text-gray-400 text-center">
           Bn c th s dng ch  khch  th nghim tnh nng m khng cn ng nhp
        </p>
      </div>
    </div>
  );
}